@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*
bg: rgb(107,147,170);

*/

html {
  background-image: linear-gradient(25deg,
  rgba(97,154,70, 0.4),
  rgba(67,128,41, 0.4)),
  url(/static/media/background.38e7e2c5.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  font-size: 15x;
}

.App {
  width: 1450px;
  height: 850px;
  margin: 0 auto;
  font-family: 'Open Sans', sans-serif;
}

.quote-container {
  display: block;
  text-align: center;
  margin: 0 auto;
}

.quote {
  font-style: italic;
  font-size: 1.3rem;
  font-family: 'Open Sans', sans-serif;
  color: rgb(94, 94, 94);
}

.quote-credit {
  font-style: normal;
  font-size: 1.1rem;
  display: block;
  font-family: 'Open Sans', sans-serif;
  color: rgb(94, 94, 94);
}

.credit {
  text-decoration: none;
  position: fixed;
  bottom: 5px;
  left: 5px;
  font-family: 'Open Sans', sans-serif;
  color: rgb(200,220,244);
}

.github-link,
.github-link:visited {
  color: rgb(200,220,244);
  text-decoration: none;
}

.github-link:hover {
  color: rgb(166, 191, 221);
}

@media only screen and (max-device-width: 550px) {
  .credit {
    position: absolute;
    top: 4px;
  }

  .quote-container {
    position: absolute;
    top: 5%;
    left: 0;
  }

  .quote {}

  .quote-credit {}
}

.display-container {
  width: 1200px;
  height: auto;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1 1;
  flex-wrap: 1;
  flex-direction: row;
}

.tutorial-container {
